import React from 'react';

import { ThemeProvider, createMuiTheme } from '@material-ui/core/styles';
import Layout from './hoc/Layout/Layout';
import FreckleForm from './components/FreckleForm/';

function App({themeObj}) {
  
  window.muiTheme = {
    palette: {
        primary: { main: '#0A2756', contrastText: "#fff" },
            secondary: { main: '#3cc5de', contrastText: "#fff" },
            white:'#fff',
            black:'#000',
            grey: { dark: '#A6A6A6', light: '#F2F2F2' },
            blue: {dark:'#0A2756', main:'#0080FF',blue2:'#0081C6',blue3:'#77B0DD'},
            green:{main:'#87CB9C'},
            red:{main:'#F47F64'},
            orange:{main:'#FFB32C'},
            error: { main: '#F47F64', contrastText: "#fff" },
            removeBoxShadow: {boxShadow: "none"},
    },
};

  const themeBuilder = themeObj ? themeObj : window.muiTheme;
  const theme = createMuiTheme(themeBuilder);

  return (
    <ThemeProvider theme={theme}>
      <div style={{height:'100%'}} >
      <Layout>
          <FreckleForm />
      </Layout>
      </div>
    </ThemeProvider>

  )
}

export default App;
