import React from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx'
import { createMuiTheme } from "@material-ui/core";
import { ThemeProvider, makeStyles } from '@material-ui/core/styles';
import DateUtils from '@date-io/date-fns'
import { 
    MuiPickersUtilsProvider,
    KeyboardDatePicker
} from '@material-ui/pickers';

const dateFns = new DateUtils();

const overrideTheme = outerTheme => createMuiTheme({
    overrides: {
      MuiPickersDay: {
        daySelected: {
          backgroundColor: outerTheme.palette.secondary.main,
        },
      },
    },
});

const useStyles = makeStyles(theme => ({
    firstDateSelector: {
        // first-of-type not working?
        paddingRight: 8
    },
    dateSelector: {
        width: 125,
    },
    day: {
      width: 36,
      height: 36,
      fontSize: '0.875rem',
      margin: "0 2px",
      color: "inherit",
    },
    highlight: {
        backgroundColor: theme.palette.secondary.main,
        color: theme.palette.common.white,
        '&:hover':{
            backgroundColor: theme.palette.primary.main,
        }
    },
    nonCurrentMonthDay: {
      color: theme.palette.text.disabled,
    },
}))

function DateSelect({startDate, endDate, handleChangeEnd, handleChangeStart,loading,rowNum}) {

    const classes = useStyles()


    return (
        <ThemeProvider theme={overrideTheme}>
            <MuiPickersUtilsProvider  utils={DateUtils}>
                  <KeyboardDatePicker
                    className={clsx(classes.dateSelector,classes.firstDateSelector)}
                    autoOk
                    maxDate={endDate ? endDate : dateFns.date()}
                    disabled={loading}
                    disableToolbar
                    variant="inline"
                    format="MM/dd/yyyy"
                    margin="normal"
                    id={`StartDate_${rowNum}`}
                    label="Start Date"
                    value={startDate}
                    onChange={handleChangeStart}
                    KeyboardButtonProps={{
                      'aria-label': 'change start date',
                    }}
                  />
                  <KeyboardDatePicker
                    className={classes.dateSelector}
                    autoOk
                    minDate={startDate}
                    maxDate={dateFns.date()}
                    disabled={loading ||typeof startDate==='undefined' || startDate === null}
                    disableToolbar
                    variant="inline"
                    format="MM/dd/yyyy"
                    margin="normal"
                    id={`EndDate_${rowNum}`}
                    label="End Date"
                    value={endDate}
                    onChange={handleChangeEnd}
                    KeyboardButtonProps={{
                      'aria-label': 'change end date',
                    }}
                  />
            </MuiPickersUtilsProvider>
        </ThemeProvider>
    );
  }

  DateSelect.propTypes = {
    startDate: PropTypes.instanceOf(Date),
    endDate: PropTypes.instanceOf(Date),
    handleChangeEnd: PropTypes.func.isRequired,
    handleChangeStart: PropTypes.func.isRequired,
    loading: PropTypes.bool.isRequired,
  };

  export default DateSelect