import React, { useState, useCallback } from "react";
export const PageContext = React.createContext();

function PageContextProvider(props) {
    const [curPage, setCurPage] = useState({page:'main',fakeUrl:`Xander's Front-End Development`,tabList:['main']});
    
    const pageNames = {
        MAIN:'main',
        AG:'ag',
        NOKO:'noko',
        TREE:'tree',
        TABLE:'table',
        EXP:'exp',
    }
    const pageUrlList = {
        'main':`Xander's Front-End Development`,
        'ag':`AG Grid Function Component`,
        'noko':`Noko Time App`,
        'tree':`Tree Component`,
        'table':`Custom Table Design`,
        'exp':`Xander's Work Experience`,
    }
    
    const handleSetCurPage = useCallback((page) => {
        setCurPage(oldObj=>{
            let updatedTabs = [...oldObj.tabList]
            if(!updatedTabs.includes(page)) {
                updatedTabs.push(page)
            }
            return{page,fakeUrl:null, tabList:updatedTabs}
        })
        setCurPage(oldObj=>{
            return{...oldObj,fakeUrl:pageUrlList[page]}
        })
    }, []);

    const handleTabRemove = useCallback((page) => {
        setCurPage(oldObj=>{
            let updatedTabs = [...oldObj.tabList].filter(val=> val !== page)
            let updatePage = oldObj.page
            let updateFakeUrl = oldObj.fakeUrl
            if(oldObj.page === page) {
                updatePage = 'main';
                updateFakeUrl = pageUrlList[page]
            }
            return{page:updatePage,fakeUrl:updateFakeUrl, tabList:updatedTabs}
        })
    }, [curPage]);

    const contextMemoData = React.useMemo(() => (
        {
            curPage,
            pageNames,
            handleSetCurPage,
            handleTabRemove,
        }),
        [
            curPage,
            pageNames,
            handleSetCurPage,
            handleTabRemove,
        ]
    );

    return (
        <PageContext.Provider value={contextMemoData}>
            {props.children}
        </PageContext.Provider>
    )
}

export default React.memo(PageContextProvider);