import React, {useState, useEffect, createRef} from 'react';
import PropTypes from 'prop-types';
import scss from './Browser.module.scss';
import clsx from 'clsx';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faTimes } from '@fortawesome/free-solid-svg-icons'
import { faPlus } from '@fortawesome/free-solid-svg-icons'
import { faArrowLeft } from '@fortawesome/free-solid-svg-icons'
import { faArrowRight } from '@fortawesome/free-solid-svg-icons'
import { faRedo } from '@fortawesome/free-solid-svg-icons'
import { faCode } from '@fortawesome/free-solid-svg-icons'
import { faMinus } from '@fortawesome/free-solid-svg-icons'
import { faSquare } from '@fortawesome/free-regular-svg-icons'
import { faStar } from '@fortawesome/free-regular-svg-icons'
import { faEllipsisV } from '@fortawesome/free-solid-svg-icons'
import MainMenu from '../MainMenu'
import Typewriter from '../Typewriter'
import Tree from '../Demos/Tree'
import Table from '../Demos/Table/Traffic'
import AG from '../Demos/AGgrid'
import Noko from '../Demos/Noko'
import Experience from '../Experience'


import {PageContext} from '../../Context/PageContext';

function NameBox(props) {

  const componentRef = createRef();

  const handleClickOutside = (event) => {
    if (event.target.innerText !== ('X') && componentRef.current && !componentRef.current.contains(event.target) ) {
      props.handleClose();
    }
  }

  useEffect(()=>{
    document.addEventListener('mousedown', handleClickOutside);

    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    }
  },[componentRef])

  return (
    <div ref={componentRef} className={clsx(scss.nameBox, [props.open && scss.nameBoxOpen ])}>
      <div ref={componentRef} className={clsx(scss.nameBoxCircle)}>X</div>
      <span className={clsx(scss.nameBoxName)}>Xan</span>
      <span className={clsx(scss.nameBoxTitle)}>Front-End Developer</span>
    </div>
  )
}
NameBox.propTypes = {
  open: PropTypes.bool.isRequired,
  handleClose: PropTypes.func.isRequired,
};

function TabRow() {

  const {
    curPage,
    pageNames,
    handleSetCurPage,
    handleTabRemove,
  } = React.useContext(PageContext) 

  const handleCloseTab = (e,page) => {
    e.preventDefault()
    e.stopPropagation()
    handleTabRemove(page)
  }
  const handleTabClick = (page) => {
    if(curPage.page === page) return
    handleSetCurPage(page)
  }

  return (
    <div className={clsx(scss.top, scss.tabRow)}>
      <div onClick={()=>handleTabClick(pageNames.MAIN)} className={clsx(scss.tab, [curPage.page === pageNames.MAIN ? scss.tab_active : scss.tabInactive])}>
        <span  className={clsx(scss.tabContent, scss.tabText)}>New Tab</span>
        <div className={clsx(scss.tabContent, scss.closeTabDiv)}>
          <FontAwesomeIcon className={clsx(scss.closeTab)} icon={faTimes}  />
        </div>
      </div>
      { curPage.tabList.includes(pageNames.AG) &&
      <div onClick={()=>handleTabClick(pageNames.AG)} className={clsx(scss.tab, [curPage.page === pageNames.AG ? scss.tab_active : scss.tabInactive])}>
        <span  className={clsx(scss.tabContent, scss.tabText)}>AG</span>
        <div onClick={(e)=>handleCloseTab(e,pageNames.AG)} className={clsx(scss.tabContent, scss.closeTabDiv)}>
          <FontAwesomeIcon className={clsx(scss.closeTab)} icon={faTimes}  />
        </div>
      </div>
      }
      { curPage.tabList.includes(pageNames.NOKO) &&
      <div onClick={()=>handleTabClick(pageNames.NOKO)} className={clsx(scss.tab, [curPage.page === pageNames.NOKO ? scss.tab_active : scss.tabInactive])}>
        <span  className={clsx(scss.tabContent, scss.tabText)}>NOKO</span>
        <div onClick={(e)=>handleCloseTab(e,pageNames.NOKO)} className={clsx(scss.tabContent, scss.closeTabDiv)}>
          <FontAwesomeIcon className={clsx(scss.closeTab)} icon={faTimes}  />
        </div>
      </div>
      }
      { curPage.tabList.includes(pageNames.TREE) &&
      <div onClick={()=>handleTabClick(pageNames.TREE)} className={clsx(scss.tab, [curPage.page === pageNames.TREE ? scss.tab_active : scss.tabInactive])}>
        <span  className={clsx(scss.tabContent, scss.tabText)}>TREE MENU</span>
        <div onClick={(e)=>handleCloseTab(e,pageNames.TREE)} className={clsx(scss.tabContent, scss.closeTabDiv)}>
          <FontAwesomeIcon className={clsx(scss.closeTab)} icon={faTimes}  />
        </div>
      </div>
      }
      { curPage.tabList.includes(pageNames.TABLE) &&
      <div onClick={()=>handleTabClick(pageNames.TABLE)} className={clsx(scss.tab, [curPage.page === pageNames.TABLE ? scss.tab_active : scss.tabInactive])}>
        <span  className={clsx(scss.tabContent, scss.tabText)}>CUSTOM TABLE</span>
        <div onClick={(e)=>handleCloseTab(e,pageNames.TABLE)} className={clsx(scss.tabContent, scss.closeTabDiv)}>
          <FontAwesomeIcon className={clsx(scss.closeTab)} icon={faTimes}  />
        </div>
      </div>
      }
      { curPage.tabList.includes(pageNames.EXP) &&
      <div onClick={()=>handleTabClick(pageNames.EXP)} className={clsx(scss.tab, [curPage.page === pageNames.EXP ? scss.tab_active : scss.tabInactive])}>
        <span  className={clsx(scss.tabContent, scss.tabText)}>WORK</span>
        <div onClick={(e)=>handleCloseTab(e,pageNames.EXP)} className={clsx(scss.tabContent, scss.closeTabDiv)}>
          <FontAwesomeIcon className={clsx(scss.closeTab)} icon={faTimes}  />
        </div>
      </div>
      }
      <FontAwesomeIcon className={clsx(scss.tabPlus)} icon={faPlus}  />
      <div className={scss.flexGrow}></div>
      <FontAwesomeIcon className={clsx(scss.tabRowIconsCorner)} icon={faMinus}  />
      <FontAwesomeIcon className={clsx(scss.tabRowIconsCorner)} icon={faSquare}  />
      <FontAwesomeIcon className={clsx(scss.tabRowIconsCorner)} icon={faTimes}  />
    </div>
  )
}
function AddressRow() {
  const [open, setOpen] = useState(false);

  const {
    curPage,
  } = React.useContext(PageContext) 

  const handleClose = () => {
    setOpen(false);
  }
  return (
    <div className={clsx(scss.top, scss.addressRow)}>
      <FontAwesomeIcon className={clsx(scss.addressIcons)} icon={faArrowLeft}  />
      <FontAwesomeIcon className={clsx(scss.addressIcons)} icon={faArrowRight}  />
      <FontAwesomeIcon className={clsx(scss.addressIcons)} icon={faRedo}  />
      <div className={clsx(scss.urlField)}>
        <FontAwesomeIcon className={clsx(scss.urlIcon)} icon={faCode}  />
        <Typewriter url={curPage.fakeUrl} />
        {/* <span>Xander's Front-End Development</span> */}
        <FontAwesomeIcon className={clsx(scss.urlIcon)} icon={faStar}  />
      </div>
      <div className={clsx(scss.addressRowRight)}>
        <div onClick={()=>{if(!open){setOpen(true)}else{setOpen(false)}}} className={clsx(scss.userCircle)}>X</div>
        <NameBox open={open} handleClose={handleClose} />
        <FontAwesomeIcon className={clsx(scss.urlIcon)} icon={faEllipsisV}  />
      </div>
    </div>
  )
}
function Content() {
  const {
    curPage,
    pageNames,
  } = React.useContext(PageContext)

  let CurrentPage

  switch (curPage.page) {
    case pageNames.MAIN:
      CurrentPage = <MainMenu />
      break;
    case pageNames.NOKO:
      CurrentPage = <Noko />
      break;
    case pageNames.TREE:
      CurrentPage = <Tree />
      break;
    case pageNames.TABLE:
      CurrentPage = <Table />
      break;
    case pageNames.AG:
      CurrentPage = <AG />
      break;
    case pageNames.EXP:
      CurrentPage = <Experience />
      break;
  
    default:
      CurrentPage = <MainMenu />
      break;
  }

  return (
    <div className={clsx(scss.content)}>
      {CurrentPage}
    </div>
  )
}
function Browser() {
  return (
    <>
    <div className={scss.placeholder}></div>
    <div className={scss.Browser}>
      <TabRow />
      <AddressRow />
      <Content />
    </div>
    </>
  );
}

export default Browser;
