import React from 'react';
import scss from './Experience.module.scss';
import { VerticalTimeline, VerticalTimelineElement }  from 'react-vertical-timeline-component';
import 'react-vertical-timeline-component/style.min.css';
// import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import LaptopIcon from '@material-ui/icons/Laptop';
import LocalCafeIcon from '@material-ui/icons/LocalCafe';
import AccountTreeIcon from '@material-ui/icons/AccountTree';
import NavigationIcon from '@material-ui/icons/Navigation';

function Experience() {

  return (
    <div className={scss.root}>
        {/* <PieSvg /> */}
      <VerticalTimeline>
        <VerticalTimelineElement
          className="vertical-timeline-element--work"
          contentStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}
          contentArrowStyle={{ borderRight: '7px solid  rgb(33, 150, 243)' }}
          date={<span className={scss.initialText}>2017 - present</span>}
          iconStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}
          icon={<LaptopIcon />}
        >
          <h3 className="vertical-timeline-element-title">Front End Developer</h3>
          <h4 className="vertical-timeline-element-subtitle">MediaCom Worldwide</h4>
          <h4 className="vertical-timeline-element-subtitle">ReactJs</h4>
          <p>
            Built Internal Dashboards, and Scripted AppScripts for Google Sheets
          </p>
        </VerticalTimelineElement>
        <VerticalTimelineElement
          className="vertical-timeline-element--work"
          date="2017"
          iconStyle={{ background: '#BD3B1F', color: '#fff' }}
          icon={<LocalCafeIcon />}
        >
          <h3 className="vertical-timeline-element-title">Freelance Web Developer</h3>
          <h4 className="vertical-timeline-element-subtitle">MeteorJs</h4>
          <p>
            Built Dashboard for client
          </p>
        </VerticalTimelineElement>
        <VerticalTimelineElement
          className="vertical-timeline-element--work"
          date="2016"
          iconStyle={{ background: '#BD3B1F', color: '#fff' }}
          icon={<LocalCafeIcon />}
        >
          <h3 className="vertical-timeline-element-title">Freelance Web Developer</h3>
          <h4 className="vertical-timeline-element-subtitle">Angular 1.5</h4>
          <p>
            Built Internal App for online school system. Utilized existing backend PHP, and MySQL. Managed In-house Developer.
          </p>
        </VerticalTimelineElement>
        <VerticalTimelineElement
          className="vertical-timeline-element--work"
          date="2014 - 2015"
          iconStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}
          icon={<LaptopIcon />}
        >
          <h3 className="vertical-timeline-element-title">Fullstack Web Developer</h3>
          <h4 className="vertical-timeline-element-subtitle">CareTech Solutions</h4>
          <h4 className="vertical-timeline-element-subtitle">C# and JS</h4>
          <p>
            Updated Large Hosptial Websites per client requests. Built Pay Connexion Payment Gateway. Created SQL nightly import. Started as an Intern for three Months.
          </p>
        </VerticalTimelineElement>
        <VerticalTimelineElement
          className="vertical-timeline-element--work"
          date="Prior to 2014"
          iconStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}
          icon={<AccountTreeIcon />}
        >
          <h3 className="vertical-timeline-element-title">Network Engineer</h3>
          <h4 className="vertical-timeline-element-subtitle">CCNP, CCNA, CCDA</h4>
          <p>
            All Prior Experience as a Cisco Certified Network Engineer
          </p>
        </VerticalTimelineElement>
        <VerticalTimelineElement
          iconStyle={{ background: 'rgb(16, 204, 82)', color: '#fff' }}
          icon={<NavigationIcon />}
        />
      </VerticalTimeline>
    </div>
  );
}

export default Experience;